import { FC, SetStateAction } from "react";
import {
    MultiSelect,
    Paper,
    SelectItem,
    Title,
} from "@mantine/core";

import type { AssetToken } from "~/moralis.server";

import TokenItem from "./TokenItem";

type Dict<T> = {
    [key: string]: T
};

export type TokenComponentProps = {
    token: AssetToken
    assetTokens: Dict<AssetToken>
    onNumberChange?: Function
};

type TokenSelectProps = {
    title: string
    assetTokens: Dict<AssetToken>
    tokenComponent: FC<TokenComponentProps>
    // selectedTokens: AssetToken[]
    // setSelectedTokens: React.Dispatch<SetStateAction<AssetToken[]>>
    // onNumberChange?: Function
    isPay: boolean
    placeholder: string
};

const TokenSelect = (props: TokenSelectProps) => {
    const { 
        title,
        assetTokens,
        tokenComponent,
        // selectedTokens,
        // setSelectedTokens,
        // onNumberChange,
        isPay,
        placeholder
    } = props;

    const TokenComponent: FC<TokenComponentProps> = tokenComponent;

    const items: SelectItem[] = Object.values(
        assetTokens
    ).filter(
        (t: AssetToken) => isPay ? !t.isReceive : !t.isPay
    ).map((t: AssetToken) => {
        return {
            label: `${t.name} (${t.symbol})`,
            value: t.address,
            token: t
        }
    });

    const handleSelect = ((v: string[]) => {
        Object.values(assetTokens).forEach((a: AssetToken) => {
            if (v.includes(a.address)) {
                a.isPay = isPay;
                a.isReceive = !isPay;
            } else {
                if (isPay) {
                    a.isPay = !isPay;
                    a.amount = 0;
                } else {
                    a.isReceive = isPay;
                    a.allocation = 0;
                };
            };
        });
        const nSelected = v.length;
        if (isPay && nSelected) {
            for (let i=0; i<nSelected; i++) {
                const oldAmount = assetTokens[v[i]].amount;
                assetTokens[v[i]].amount = oldAmount == 0 ? 1 : oldAmount;
            }
        } else if (!isPay && nSelected) {
            let totalAllocation = 0;
            for (let i=0; i<nSelected-1; i++) {
                totalAllocation += assetTokens[v[i]].allocation;
            }
            assetTokens[v[nSelected-1]].allocation = Math.max(0,100-totalAllocation);
        }
        setSelectedTokens(v.map(
                (address: string) => assetTokens[address]
        ));
        onNumberChange && onNumberChange();
    });

    return (
        <Paper withBorder p="xl" radius="md" mt="lg">
            <Title order={4} align="center">{title}</Title>
            {
                Object.values(assetTokens)
                    .filter((token: AssetToken) => isPay ? token.isPay : token.isReceive)
                    .map((token: AssetToken, i: number) => {
                        return (
                            <TokenComponent
                                token={token}
                                assetTokens={assetTokens}
                                onNumberChange={onNumberChange}
                                key={token.address}
                            />
                        )
                    })
            }
            {/* <Text color="dimmed" mt="lg">Select tokens to deposit:</Text> */}
            <MultiSelect
                data={items}
                // label="Select tokens to deposit:"
                itemComponent={TokenItem}
                // valueComponent={() => null}
                value={selectedTokens.map((token: AssetToken) => token.address)}
                onChange={handleSelect}
                mt="xs"
                size="md"
                placeholder={placeholder}
                searchable
                nothingFound="Nothing found"
                clearable
                clearButtonLabel="Clear selected tokens"
            />
        </Paper >
    );
};

export default TokenSelect;
